import * as yup from "yup";

export const schema = yup
  .object({
    name: yup
      .string()
      .min(4, "     4 ")
      .max(64, "     64 ")
      .required("  "),
    text: yup
      .string()
      .min(6, "     6 ")
      .max(200, "     200 ")
      .required("  "),
    captcha: yup
      .string()
      .min(5, "   4 ")
      .max(5, "   4 ")
      .required("  "),
  })
  .required();
