import * as yup from "yup";

import { Regulars } from "shared/lib";

//  e-mail 0_0
export const schema = yup
  .object({
    login: yup
      .string()
      .matches(
        Regulars.regEmail,
        "E-mail   , ,  ,  @    "
      )
      .required("  "),
    password: yup
      .string()
      .min(8, "     8 ")
      .max(24, "     24 ")
      .matches(
        Regulars.regPassword,
        "   , ,  ,  ( ., -, _ ...)"
      )
      .required("  "),
  })
  .required();
