import React from "react";
import { SubmitHandler, useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";

import { Types } from "shared/lib";
import { Button, Input } from "shared/ui";

import { submitAuthForm } from "../model";
import { schema } from "../lib/schema";

import "./style.scss";

export const AuthForm: React.FC = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<Types.IFormInputs>({
    resolver: yupResolver(schema),
    mode: "onChange",
  });

  const onSubmit: SubmitHandler<Types.IFormInputs> = async (data) => {
    await submitAuthForm({ email: data.login, password: data.password });
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="auth-form">
      <Input
        name="login"
        label=""
        placeholder=" "
        register={register}
        id="login"
        required
        error={!!errors.login}
        errorMessage={errors.login?.message}
      />
      <Input
        maxlength={24}
        typeInput="password"
        name="password"
        label=""
        placeholder=" "
        register={register}
        id="password"
        required
        error={!!errors.password}
        errorMessage={errors.password?.message}
      />
      <Button type="submit" disabled={!!(errors?.password || errors?.login)}>
        
      </Button>
    </form>
  );
};
