import * as yup from "yup";

import { Regulars } from "shared/lib";

export const schema = yup
  .object({
    firstName: yup
      .string()
      .min(2, "     2 ")
      .max(40, "     40 ")
      .matches(
        Regulars.regName,
        "       "
      ),
    lastName: yup
      .string()
      .min(2, "     2 ")
      .max(40, "     40 ")
      .matches(
        Regulars.regName,
        "       "
      ),
    dateBirth: yup
      .string()
      .matches(Regulars.regDate, "    : .."),
    city: yup.object({
      label: yup.string(),
      value: yup.string(),
    }),
    sex: yup.object({
      label: yup.string(),
      value: yup.string(),
    }),
    pet: yup.object({
      label: yup.string(),
      value: yup.string(),
    }),
    text: yup.string(),
    shortInfo: yup
      .string()
      .max(99, "     200 "),
  })
  .required();
